/*  <buttons>  */
.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 62px;
  color: #0098dc;
  font-size: 2.4rem;
  background-color: #fff;
  border: 0;
  border-radius: 31px;
  padding: 0 20px;
  box-shadow: 0 5px 0 #d8d7d7;
  cursor: pointer;
  transition: all 0.2s;
}
.btn:hover {
  text-decoration: none;
  background-color: #f2f2f2;
}
.btn:active {
  background-color: #f2f2f2;
  box-shadow: 0 5px 0 transparent;
  transform: translateY(3px);
}
.btn--orange {
  color: #fff;
  background-color: #ed8223;
  box-shadow: 0 5px 0 #bb661a;
}
.btn--orange:hover,
.btn--orange:active {
  background-color: #e47513;
}
/*  </buttons>  */
/*  <inputs>  */
.input-text {
  width: 100%;
  height: 56px;
  font-size: 1.8rem;
  background-color: #fff;
  border: 0;
  padding: 0 15px;
}
.input-text::placeholder {
  opacity: 1;
  color: #292929;
}
.input-text--icon {
  padding-left: 52px;
}
/*  </inputs>  */
/*  <layout>  */
.form__row {
  margin-bottom: 15px;
}
.form__row:first-child {
  margin-top: 0;
}
.form__row:last-child {
  margin-bottom: 0;
}
.form__row--icon {
  position: relative;
}
.form__row--icon .input-text {
  padding-left: 52px;
}
.form__row--icon .input-text:focus + svg {
  fill: #ed8223;
}
.form__row--icon svg {
  position: absolute;
  top: 50%;
  left: 11px;
  width: 30px;
  height: 30px;
  fill: #292929;
  transform: translateY(-50%);
  transition: all 0.2s;
}
/*  </layout>  */
/*  <animation>  */
/*  </animation>  */
@-moz-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
