.homepage-cert__items .slick-arrow {
  position: absolute;
  top: 50%;
  width: 30px;
  height: 30px;
  text-indent: -99999px;
  background: none;
  border: solid #3f4040;
  cursor: pointer;
  transition: all 0.2s;
}
.homepage-cert__items .slick-arrow:hover {
