}
.homepage-cert__items .slick-prev {
  left: -30px;
  border-width: 3px 0 0 3px;
  transform: rotate(-45deg) translateY(-50%);
}
.homepage-cert__items .slick-next {
  right: -30px;
  border-width: 3px 3px 0 0;
  transform: rotate(45deg) translateY(-50%);
}
.homepage-cert__item img {
